import React, { useState, useEffect } from "react";
import { TextField, Button, Divider } from "@material-ui/core";
import "./CSS/register.css";
import logo from "../assets/logo/logo.png";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import axios from "axios";
import { Link } from "react-router-dom";
import Cookies, { set } from "js-cookie";
import CardSection from "./Components/CardSection";
import { useStripe, useElements, CardElement } from "@stripe/react-stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import PaymentCard from "./Components/PaymentCard";
import moment from "moment";

export default function Login({ history }) {
  const stripePromise = loadStripe(
    "pk_test_51HPDRdHciKBv1ntWsxnVf3HHzsqfOXaJh7BxIKzY7yDGMjyPEz1CM0h8Lm9wHwqxqmceCyZCvEvHP3UtpIqvD2dP00eJD03Olv"
  );

  const theme = createMuiTheme({
    palette: {
      primary: {
        main: "#2c1951",
      },
      secondary: {
        main: "#f2f2f7",
      },
    },
  });
  function getCookie(name) {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  }
  function listenCookieChange(callback, interval = 1000) {
    let lastCookie = document.cookie;
    setInterval(() => {
      let cookie = document.cookie;
      if (cookie !== lastCookie) {
        try {
          callback({ oldValue: lastCookie, newValue: cookie });
        } finally {
          lastCookie = cookie;
        }
      }
    }, interval);
  }

  listenCookieChange(({ oldValue, newValue }) => {
    setCurrentSelected(
      document.cookie
        .split("; ")
        .find((row) => row.startsWith("subType"))
        .split("=")[1]
    );
  }, 1000);

  let [currentSelected, setCurrentSelected] = useState(0);
  const [payment, setPayment] = useState(false);
  const [updateSub, setUpdateSub] = useState(false);
  const [user, setUser] = useState({
    email: "",
    firstName: "",
    lastName: "",
    subType: "",
    cardType: "",
    cardNum: "",
    expDate: "",
    stripeID: "",
    methodID: "",
    endDate: "",
  });
  const RenderPayment = () => {
    const stripe = useStripe();
    const elements = useElements();

    const handlePayment = async () => {
      console.log(user);
      if (!payment) {
        setPayment(true);
      } else {
        const card = elements.getElement(CardElement);
        const result = await stripe.createToken(card);
        await axios
          .post("https://frca-reveal-backend.herokuapp.com/updatePayment", {
            paymentMethod: user.methodID,
            token: result.token.id,
            customerID: user.stripeID,
          })
          .then((response) => {
            console.log(response);
            window.location.reload();
          });
      }
    };
    if (payment) {
      return (
        <div>
          <Divider light={false} variant="fullWidth" />
          <CardSection />

          <div className="payment-text">
            <ThemeProvider theme={theme}>
              <div className="inlineButton">
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handlePayment}
                  margin="small"
                  size="small"
                  fullWidth
                >
                  Update
                </Button>
              </div>
            </ThemeProvider>
          </div>
        </div>
      );
    } else {
      return (
        <div className="payment-text">
          <ThemeProvider theme={theme}>
            <div className="inlineButton">
              <Button
                variant="contained"
                color="secondary"
                onClick={handlePayment}
                margin="small"
                size="small"
                fullWidth
              >
                Update
              </Button>
            </div>
          </ThemeProvider>
        </div>
      );
    }
  };
  const RenderSub = () => {
    const [text, setText] = useState("");
    const handleSub = async () => {
      setText("Loading...");
      console.log(user);
      await axios
        .post("https://frca-reveal-backend.herokuapp.com/updatePay", {
          customerID: user.stripeID,
          paymentID: user.methodID,
          subType: parseInt(currentSelected),
        })
        .then((response) => {
          console.log(response);
          window.location.reload();
        })
        .catch((err) => {
          console.log(err);
          setText("Error");
        });
    };
    if (updateSub) {
      return (
        <div>
          <div className="payment-cards">
            <PaymentCard months={1} price="£14" />
            <PaymentCard months={2} price="£22" />
            <PaymentCard months={3} price="£30" />
            <PaymentCard months={6} price="£40" />
          </div>

          <Divider />
          <b>
            {currentSelected
              ? "You have selected the " + currentSelected + " month package"
              : "Please select a package"}{" "}
          </b>
          <br />
          <br />

          <div className="payment-text">
            <ThemeProvider theme={theme}>
              <div className="inlineButton">
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={handleSub}
                  margin="small"
                  size="small"
                  fullWidth
                >
                  Update
                </Button>
              </div>
            </ThemeProvider>
          </div>
          {text}
        </div>
      );
    } else {
      return (
        <div className="payment-text">
          <ThemeProvider theme={theme}>
            <div className="inlineButton">
              <Button
                variant="contained"
                color="secondary"
                onClick={() => {
                  setUpdateSub(true);
                }}
                margin="small"
                size="small"
                fullWidth
              >
                Update
              </Button>
            </div>
          </ThemeProvider>
        </div>
      );
    }
  };
  useEffect(() => {
    getUser();
  }, []);
  const handleLogout = () => {
    Cookies.remove("accessToken");

    history.push({ pathname: "/" });
    window.location.reload();
  };

  const getUser = async () => {
    await axios
      .get("https://frca-reveal-backend.herokuapp.com/loginToken", {
        headers: {
          authorization: `Bearer ${getCookie("accessToken")}`,
        },
      })
      .then(async (res) => {
        await axios
          .get("https://frca-reveal-backend.herokuapp.com/getPayment", {
            headers: {
              authorization: `Bearer ${getCookie("accessToken")}`,
            },
          })
          .then((response) => {
            console.log(response.data.data[0] == null);
            const date = moment(res.data.user.createDate, "YYYY-MM-DD");
            const endDate = date.add(res.data.user.subType * 30, "days");
            console.log(endDate);
            setUser({
              email: res.data.user.email,
              firstName: res.data.user.firstName,
              lastName: res.data.user.lastName,
              subType: res.data.user.subType,
              cardType:
                response.data.data[0] != null
                  ? response.data.data[0].card.brand
                  : "",
              cardNum:
                response.data.data[0] != null
                  ? "****" + response.data.data[0].card.last4
                  : "No Card Exists Please Update",
              stripeID: res.data.user.stripeID,
              methodID:
                response.data.data[0] != null ? response.data.data[0].id : "",
              expDate:
                response.data.data[0] != null
                  ? response.data.data[0].card.exp_month +
                    "/" +
                    response.data.data[0].card.exp_year
                  : " ",
              endDate: endDate.format("DD/MM/YYYY"),
            });
          })
          .catch((err) => {
            console.log(err);
          });
      })
      .catch(async (err) => {
        console.log(err);
      });
  };
  return (
    <div className="fullBody">
      <div className="container">
        {/* <img src={logo} alt="Logo" className="logo"></img> */}
        <form className="formContainer">
          <label className="label">
            <u>Personal Details</u>
          </label>

          <label className="label">First Name</label>
          <Divider light={false} variant="fullWidth" />
          <div className="display-details">{user.firstName}</div>

          <label className="label">Last Name</label>
          <Divider light={false} variant="fullWidth" />
          <div className="display-details">{user.lastName}</div>

          <label className="label">Email</label>
          <Divider light={false} variant="fullWidth" />
          <div className="display-details">{user.email}</div>

          <label className="label">Subscription Type</label>
          <Divider light={false} variant="fullWidth" />
          <div className="display-details">
            {user.subType > 0
              ? user.subType + " Month Package"
              : "No Package Selected"}
          </div>
          <div className="display-details">
            {"Package Ends: " + user.endDate}
          </div>
          <RenderSub />
          <br />
          <label className="label">
            <u>Payment Details</u>
          </label>

          <label className="label">Card Type</label>
          <Divider light={false} variant="fullWidth" />
          <div className="payment-details">
            <div className="payment-text">
              {"Card Type: " +
                user.cardType.charAt(0).toUpperCase() +
                user.cardType.slice(1)}
            </div>
            <div className="payment-text">{"Card Number: " + user.cardNum}</div>

            <div className="payment-text">{"Expiry Date: " + user.expDate}</div>

            <Elements stripe={stripePromise}>
              <RenderPayment />
            </Elements>
          </div>

          <ThemeProvider theme={theme}>
            <div className="buttonContainer">
              <Button
                variant="contained"
                color="primary"
                onClick={handleLogout}
                margin="normal"
                fullWidth
              >
                Logout
              </Button>
            </div>
          </ThemeProvider>
        </form>
      </div>
    </div>
  );
}
